.meaningoverview{
  position: relative;
}

.meaningoverview__grid{
  width: 900px;
  position: relative;
}

.meaningoverview__table{
  width: 100%;
  padding-top: 1em;
}

.meaningoverview__table td{
  padding: 1em 1.5em;
}

.meaningoverview__table thead td{
  font-weight: 600;
  padding: 1em 1em;
}

.meaningoverview__meaningentry:nth-of-type(odd) {
  background-color: #eee;
}

.meaningoverview__meaningentry > a{
  display: inline-block;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.meaningoverview__meaningentry{
  -webkit-transition: -webkit-transform 0.2s ease;
  transition: -webkit-transform 0.2s ease;
  -o-transition: transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
}

.meaningoverview__meaningentry:hover{
  -webkit-transform: scale(1.02);
      -ms-transform: scale(1.02);
          transform: scale(1.02);
  -webkit-transition: -webkit-transform 0.2s ease;
  transition: -webkit-transform 0.2s ease;
  -o-transition: transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
}


.meaningoverview__create{
  position: absolute;
  right: 0;
}


