.lemmaoverview{
  position: relative;
}

.lemmaoverview__grid{
  width: 900px;
  position: relative;
}

.lemmaoverview__table{
  width: 100%;
  padding-top: 1em;
}

.lemmaoverview__table td{
  padding: 1em 1.5em;
}

.lemmaoverview__table thead th{
  font-weight: 400;
  padding: 1em 1em;
  text-align: left;
}

.lemmaoverview__lemmaentry:nth-of-type(odd) {
  background-color: #f8f8f8;
}

.lemmaoverview__lemmaentry > a{
  display: inline-block;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.lemmaoverview__lemmaentry{
  -webkit-transition: -webkit-transform 0.2s ease;
  transition: -webkit-transform 0.2s ease;
  -o-transition: transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
}

.lemmaoverview__lemmaentry:hover{
  -webkit-transform: scale(1.02);
      -ms-transform: scale(1.02);
          transform: scale(1.02);
  -webkit-transition: -webkit-transform 0.2s ease;
  transition: -webkit-transform 0.2s ease;
  -o-transition: transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
}


.lemmaoverview__create{
  position: absolute;
  right: 0;
}


